.itwork-video {
    display: grid;
    justify-content: center;
    align-content: center;
    margin-bottom: 3rem;
    text-align: center;
}

.itwork-content {
    width: var(--container-width-lg);
    margin: auto;
    font-size: 17px;
    min-height: calc(100vh - 50px - 50px);
}

.itwork-content h1, h3 {
    text-align: center;
}

.itwork-content h5 {
    text-align: center;
    margin-bottom: 3rem;
}

.itwork-results {
    color: var(--color-white);
    background: var(--color-primary-variant);
    padding: 1rem;
    border: 1px solid transparent;
    border-radius: 1rem;
    border-color: var(--color-primary);
    margin-bottom: 3rem;
}

.itwork-transcript {
    font-size: 16px;
    color: var(--color-white);
    background: var(--color-primary-variant);
    padding: 1rem;
    border: 1px solid transparent;
    border-radius: 1rem;
    border-color: var(--color-primary);
    margin-bottom: 3rem;
}

.itwork-content h2 {
    text-align: center;
    margin-bottom: 1.5rem;
}

